import * as React from 'react';
import type { ComponentsPropsList } from '../styles/props';

export interface Localization {
  props?: {
    MuiBreadcrumbs?: Pick<ComponentsPropsList['MuiBreadcrumbs'], 'expandText'>;
    MuiTablePagination?: Pick<
      ComponentsPropsList['MuiTablePagination'],
      'labelRowsPerPage' | 'labelDisplayedRows' | 'getItemAriaLabel'
    >;
    // The core package has no dependencies on the @material-ui/lab components.
    // We can't use ComponentsPropsList, we have to duplicate and inline the definitions.
    MuiRating?: {
      emptyLabelText?: string;
      getLabelText?: (value: number) => string;
    };
    MuiAutocomplete?: {
      clearText?: string;
      closeText?: string;
      loadingText?: React.ReactNode;
      noOptionsText?: React.ReactNode;
      openText?: string;
    };
    MuiAlert?: {
      closeText?: string;
    };
    MuiPagination?: {
      'aria-label'?: string;
      getItemAriaLabel?: (type: string, page: number, selected: boolean) => string;
    };
  };
}

export const azAZ: Localization = {
  props: {
    MuiBreadcrumbs: {
      expandText: 'Yolu gstr',
    },
    MuiTablePagination: {
      getItemAriaLabel: (type) => {
        if (type === 'first') {
          return 'Birinci shify ke';
        }
        if (type === 'last') {
          return 'Sonuncu shify ke';
        }
        if (type === 'next') {
          return 'Nvbti shify ke';
        }
        // if (type === 'previous') {
        return 'vvlki shify ke';
      },
      labelRowsPerPage: 'Shify dn strlr:',
      labelDisplayedRows: ({ from, to, count }) =>
        `${from}-${to} dn ${count !== -1 ? count : `more than ${to}`}`,
    },
    MuiRating: {
      getLabelText: (value) => {
        let pluralForm = 'Ulduz';
        const lastDigit = value % 10;

        if (lastDigit > 1 && lastDigit < 5) {
          pluralForm = 'Ulduzlar';
        }

        return `${value} ${pluralForm}`;
      },
      emptyLabelText: 'Bo',
    },
    MuiAutocomplete: {
      clearText: 'Silmk',
      closeText: 'Balamaq',
      loadingText: 'Yklnir',
      noOptionsText: 'Seimlr mvcud deyil',
      openText: '',
    },
    MuiAlert: {
      closeText: 'Balamaq',
    },
    MuiPagination: {
      'aria-label': 'Shifnin naviqasiyas',
      getItemAriaLabel: (type, page, selected) => {
        if (type === 'page') {
          return `${page} ${selected ? 'shif' : 'shify ke'}`;
        }
        if (type === 'first') {
          return 'Birinci shify ke';
        }
        if (type === 'last') {
          return 'Sonuncu shify ke';
        }
        if (type === 'next') {
          return 'Nvbti shify ke';
        }
        // if (type === 'previous') {
        return 'vvlki shify ke';
      },
    },
  },
};

